/* eslint-env browser */
(function () {
  'use strict';

  var $ = window.jQuery;

  // Check to make sure service workers are supported in the current browser,
  // and that the current page is accessed from a secure origin. Using a
  // service worker from an insecure origin will trigger JS console errors. See
  // http://www.chromium.org/Home/chromium-security/prefer-secure-origins-for-powerful-new-features
  var isLocalhost = Boolean(window.location.hostname === 'localhost' ||
    // [::1] is the IPv6 localhost address.
    window.location.hostname === '[::1]' ||
    // 127.0.0.1/8 is considered localhost for IPv4.
    window.location.hostname.match(
      /^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/
    )
  );

  if ('serviceWorker' in navigator &&
    (window.location.protocol === 'https:' || isLocalhost)) {
    navigator.serviceWorker.register('service-worker.js')
      .then(function (registration) {
        // updatefound is fired if service-worker.js changes.
        registration.onupdatefound = function () {
          // updatefound is also fired the very first time the SW is installed,
          // and there's no need to prompt for a reload at that point.
          // So check here to see if the page is already controlled,
          // i.e. whether there's an existing service worker.
          if (navigator.serviceWorker.controller) {
            // The updatefound event implies that registration.installing is set:
            // https://slightlyoff.github.io/ServiceWorker/spec/service_worker/index.html#service-worker-container-updatefound-event
            var installingWorker = registration.installing;

            installingWorker.onstatechange = function () {
              switch (installingWorker.state) {
                case 'installed':
                  // At this point, the old content will have been purged and the
                  // fresh content will have been added to the cache.
                  // It's the perfect time to display a "New content is
                  // available; please refresh." message in the page's interface.
                  break;

                case 'redundant':
                  throw new Error('The installing ' +
                    'service worker became redundant.');

                default:
                // Ignore
              }
            };
          }
        };
      }).catch(function (e) {
      console.error('Error during service worker registration:', e);
    });
  }

  // Your custom JavaScript goes here
  $(document).ready(function () {

    // SLIDERS

    // Slider Introdução
    $('.intro-slider').slick({
      infinite: false,
      arrows: false,
      dots: true,
      fade: false,
      mobileFirst: true,
      focusOnSelect: false,
      autoplay: true
    });

    // Slider Cadastro
    $('.cadastro-slider').slick({
      infinite: false,
      arrows: false,
      dots: false,
      fade: false,
      mobileFirst: true,
      focusOnSelect: false,
      autoplay: false,
      draggable: false
    });

    // Slider Home
    $('.home-slider').slick({
      infinite: false,
      arrows: false,
      dots: false,
      fade: false,
      mobileFirst: true,
      focusOnSelect: false,
      autoplay: false,
      draggable: false
    });

    // Slider Home
    $('.home-slider-vendedor').slick({
      infinite: false,
      arrows: false,
      dots: false,
      fade: false,
      mobileFirst: true,
      focusOnSelect: false,
      autoplay: true,
      draggable: false
    });

    // Semantic UI JS
    //    Dados Pesquisa
    var infoVendedores = [
        {
          title: 'Renato Castro',
          description: 'Melancia, Laranja, Morango',
        },
        {
          title: 'Murilo Campos',
          description: 'Ovos Caipira e de Codorna',
        },
        {
          title: 'Cecília dos Santos',
          description: 'Churros',
        },
        {
          title: 'Diogo Levi',
          description: 'Produtos de Limpeza',
        },
        {
          title: 'Miguel monteiro',
          description: 'Peixe fresco',
        }
      ];

    //    Ativa pesquisa
    $('.ui.search')
      .search({
        source: infoVendedores,
        searchFields: [
          'title', 'description'
        ],
        searchFullText: true
      });

    // Ativa Dropdown dos Bancos
    $('.search.dropdown').dropdown();

    // Ativa as estrelas de classificação de vendedores
    $('.ui.rating').rating({
      initialRating: 4,
      maxRating: 5
    });

    // Ativa checkbox
    $('.ui.checkbox').checkbox();


    // Botão que passa slider
    $('.proximo-passo').on('click', function (e) {
      e.preventDefault();

      $('.dimmer').toggleClass('active');

      setTimeout(function () {
        $('.dimmer').toggleClass('active');

        $('.cadastro-slider').slick('slickNext');
        $('.home-slider').slick('slickNext');
        $('.home-slider-vendedor').slick('slickNext');
      }, 1000);
    });

    // Botão que volta slider
    $('.btn-back').on('click', function (e) {
      e.preventDefault();

      $('.dimmer').toggleClass('active');

      setTimeout(function () {
        $('.dimmer').toggleClass('active');

        $('.cadastro-slider').slick('slickPrev');
        $('.home-slider-vendedor').slick('slickPrev');
      }, 1000);
    });

    $('.btn-login').click(function(e){
      e.preventDefault();

      location.href='home-comprador.html';
    });

    // Botão que volta slider
    $('.btn-home').on('click', function (e) {
      e.preventDefault();

      $('.dimmer').toggleClass('active');

      setTimeout(function () {
        $('.dimmer').toggleClass('active');

        $('.home-slider').slick('slickGoTo', 0);
        $('.home-slider-vendedor').slick('slickGoTo', 0);
      }, 600);
    });

    $('.form-test button').on('click', function (e) {
      e.preventDefault();
    });

    // Alterações da página através de eventos do Slider
    $('.cadastro-slider').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      if (nextSlide === 0 || nextSlide === 1) {
        $('.ui.raised.blue.inverted.segment').show();
        $('.tela').css({
          'background': '#1B1C1D',
          'text-align': 'left',
          'color': '#fff'
        });

        if (nextSlide === 1) {
          $('.step-bar').html('2/3');
        }
      } else if (nextSlide === 2 || nextSlide === 3) {
        $('.ui.raised.blue.inverted.segment').hide();
        $('.tela').css({
          'background': 'url(../images/intro-bg.jpg) top left no-repeat',
          'text-align': 'center',
          'color': '#111'
        });
      } else if (nextSlide === 4 || nextSlide === 5) {
        $('.ui.raised.blue.inverted.segment').show().css('text-align', 'left');
        $('.tela').css({
          'background': '#1B1C1D',
          'text-align': 'center',
          'color': '#fff'
        });
        $('.step-bar').html('3/3');
      }
    });

    // On edge hit
    $('.intro-slider').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      if (nextSlide === 2) {
        $('.btn-pular').html('<i class="right checkmark icon"></i>VAMOS LÁ!').removeClass('primary').addClass('positive');
        slick.slickPause();
      } else {
        $('.btn-pular').html('<i class="right icon forward"></i>PULAR INTRODUÇÃO').removeClass('positive').addClass('primary');
      }
    });

    $('.home-slider').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      if (nextSlide === 1) {
        $('.barra-pagamento').hide();
        $('.barra-menu-busca').hide();
        $('.barra-vendedores-perto').show();

        $('.home-comprador').css('background', 'none');
      } else if (nextSlide === 2) {
        $('.barra-pagamento').hide();
        $('.barra-vendedores-perto').hide();
        $('.barra-menu-busca').hide();

        $('.home-comprador').css('background', 'url(../images/intro-bg.jpg) top left no-repeat');
      } else if (nextSlide === 3 || nextSlide === 4) {
        $('.barra-pagamento').show();

        $('.home-comprador').css('background', 'none');
      } else {
        $('.barra-pagamento').hide();
        $('.barra-vendedores-perto').hide();
        $('.barra-menu-busca').show();

        $('.home-comprador').css('background', 'url(../images/map-bg.jpg) top left no-repeat');
      }
    });

    $('.home-slider-vendedor').on('beforeChange', function (event, slick, currentSlide, nextSlide) {
      if (nextSlide === 1) {
        $('.barra-menu-busca').hide();
        $('.barra-novo-chamado').show();
        $('.barra-vendas').hide();

        $('.home-vendedor').css('background', 'url(../images/map-vendedor-cliente-bg.jpg) top left no-repeat');
        $('.home-slider-vendedor').slick('slickPause');
      } else if (nextSlide === 2) {
        $('.barra-novo-chamado').hide();
        $('.barra-menu-busca').hide();
        $('.barra-vendas').hide();
        $('.barra-enviar-cobranca').show();

        $('.home-vendedor').css('background', 'none');
      } else if (nextSlide === 3) {
        $('.barra-novo-chamado').hide();
        $('.barra-menu-busca').hide();
        $('.barra-vendas').hide();
        $('.barra-enviar-cobranca').show();

        $('.home-vendedor').css('background', 'none');
      } else if (nextSlide == 4) {
        $('.barra-novo-chamado').hide();
        $('.barra-menu-busca').hide();
        $('.barra-enviar-cobranca').hide();
        $('.barra-vendas').show();

        $('.home-vendedor').css('background', 'none');
      } else {
        $('.barra-novo-chamado').hide();
        $('.barra-menu-busca').show();
        $('.barra-vendas').hide();

        $('.home-vendedor').css('background', 'url(../images/map-vendedor-bg.jpg) top left no-repeat');
      }
    });

    /* function zoom() {
     document.body.style.zoom = "90%";
     }

     $(document).onload(zoom());*/
  });


})();
